:root {
  --color-white: #fff;
  --color-light: #fafafa;
  --color-lightgray: #1111111a;
  --color-gray: #111111b2;
  --color-dark: #1e1823;
  --color-black: #111;
  --color-orange: #fd9222;
  --color-lightorange: #fcf1e0;
}

body {
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.container {
  width: 375px;
  margin: 0 auto;
  padding: 20px;
}

button {
  cursor: pointer;
}

.second-heading {
  letter-spacing: -.03em;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 1.2;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}







